<template>
  <div id="banner">
    <el-space direction="vertical" :size="20" style="width: 100%">
      <!-- el-space为空白空间区域 direction为间隔排列方式 size为两个标签的间隔距离-->
      <h1>轮播图管理</h1>
      <div style="text-align: right">
        <el-button type="primary" @click="onAddButtodClick"><!--@click为按钮绑定点击事件 -->
          <el-icon><plus /></el-icon>
          添加轮播图
        </el-button>
      </div>
      <el-table :data="banners" style="width: 100%"><!--以表格的形式展示轮播图列表  参考EP库中Table表格组件  data指定数据为banners列表-->
        <el-table-column prop="name" label="名称" width="200px" />
        <el-table-column label="图片"  >
          <template #default="scope"> <!--自定义标签，显示轮播图图片而不是url -->
            <img :src="formatImageUrl(scope.row.image_url)" style="width: 200px;height: 60px;"/>
          </template>
        </el-table-column>
        <el-table-column  label="跳转链接" >
          <template #default="scope"> <!--自定义标签，实现点击跳转链接 -->
            <a :href="scope.row.link_url" target="_blank">{{ scope.row.link_url }}</a><!--target="_blank"实现在新的页面中打开 -->
          </template>
        </el-table-column>
        <el-table-column prop="priority" label="优先级" width="100px"/>
        <el-table-column >
          <template #default="scope"> <!--自定义标签，添加编辑和删除按钮 -->
            <el-button type="primary"  circle @click="onEditEvent(scope.$index)"><!--点击事件传入索引scope.$index 以便分辨调整的是哪张轮播图 -->
              <el-icon><edit/></el-icon>
            </el-button>
            <el-button type="danger"  circle @click="onDeleteEvent(scope.$index)">
              <el-icon><delete/></el-icon>
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-space>
    <!-- 添加EP组件库中的dialog表单对话框 该对话框默认隐藏只有某个属性得到修改才会显示-->
    <el-dialog v-model="bannderDialogVisible" title="添加/修改轮播图" width="30%">
      <el-form :model="form" :rules="rules" ref="dialogForm"> <!-- :rules="rules" 将data中的表单验证规则rules与form绑定  ref="dialogForm"将表单验证方法dialogForm与form绑定-->
        <el-form-item label="名称" prop="name"><!-- prop="name" 名称的规则与data中表单中的name绑定-->
          <el-input v-model="form.name" autocomplete="off" />
        </el-form-item>
        <el-form-item label="图片" prop="image_url">
          <div style="display: flex;">
            <el-input v-model="form.image_url" autocomplete="off" style="margin-right: 10px;"/><!-- style="margin-right: 10px 设置向右间隔-->
            <el-upload
              :action="$http.server_host+'/cmsapi/banner/image/upload'"
              :headers="{'Authorization': 'Bearer '+$auth.token}"
              name="image"
              :show-file-list="false"
              accept ="image/jpeg, image/png" 
              :on-success="onImageUploadSuccess"
              :on-error="onImageUploadError"
            ><!-- action为图片上传路径(后端提供) multiple允许上传多个文件 limit上传文件最多几个 file-list上传完成以后的保存地址 :on-xxx=执行的函数-->
            <!--:show-file-list="false" 解决上传图片对话框跳动的小bug , name="image"名称来自验证表单  on-success是上传成功执行的函数 on-error是上传失败执行的函数-->
              <el-button type="small">上传图片</el-button>
            </el-upload><!-- el-upload组件上传图片的post请求由该组件自己发出，他没有穿csrftoken,会导致400/401错误,需要设置请求头headers-->
          </div>
        </el-form-item>
        <el-form-item label="跳转" prop="link_url">
          <el-input v-model="form.link_url" autocomplete="off" />
        </el-form-item>
        <el-form-item label="优先级" prop="priority">
          <el-input v-model="form.priority" autocomplete="off"  type="number"/><!-- type="number" 指定输入类型为数值-->
        </el-form-item>
      </el-form>
      <template #footer>
        <div class="dialog-footer">
          <el-button @click="bannderDialogVisible = false">取消</el-button>
          <el-button type="primary" @click="onDialogSubmitEvent">确认</el-button>
        </div>
      </template>
    </el-dialog>
    <!-- 删除轮播图确认对话框 -->
    <el-dialog
      v-model="deleteDialogVisible"
      title="提示"
      width="30%"
    >
      <span>您确定要删除这个轮播图吗？</span>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="deleteDialogVisible = false">取消</el-button>
          <el-button type="primary" @click="onConfirmDeleteEvent">确定</el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import { Plus ,Edit ,Delete } from "@element-plus/icons";
import { ElMessage } from 'element-plus'; //导入消息提醒反馈组件（表单验证通过与否） 使用方法参考EP库中的form表单->反馈组件
export default {
  name: "BannerPage",
  components: {
    Plus,
    Edit,
    Delete
  },
  data(){
    return{
      bannderDialogVisible:false, //vue的固定写法，该组件默认不显示
      deleteDialogVisible:false, //删除轮播图的对话框也默认不显示
      deleteingIndex:0, //正在删除的索引值，默认给0
      banners:[],   //轮播图列表存放所有轮播图
      editingIndex:0,  //轮播图索引
      form:{
        name:"",
        image_url:"",
        link_url:"",
        priority:0
      },  //error:undefined reading 'name' 是name前面的那个东西未定义
      rules:{ //轮播图表单校验规则    使用方法参考EP库中的form表单
        name:[{ required: true, message: '请输入轮播图名称', trigger: 'blur' }], //trigger: 'blur' 表示失去焦点提醒
        image_url:[{ required: true, message: '请上传轮播图', trigger: 'blur' }],
        link_url:[{ required: true, message: '请输入轮播图跳转链接', trigger: 'blur' }],
        priority:[
          { required: true, message: '请输入轮播图优先级', trigger: 'blur' },
          //{type:"number",message:'轮播图优先级是一个数字',trigger:'blur'}, //优先级有两条规则
        ],
      }
    }
  },
  mounted() {
    this.$http.getBannerList().then(res =>{ //挂载轮播图列表
      //console.log(res)
      if(res['code']==200){
        let banners = res['data'];//没有权限时，data内没东西会导致报错
        this.banners=banners  //轮播图数据与banners:[] 绑定完成
      }else{
        ElMessage.error(res['message']);
      }
    })
  },
  methods:{
    formatImageUrl(image_url){ //格式化图片URL
      if(image_url.startsWith("http")){
        return image_url;   //添加的图片是个完整的http链接就不用拼接了
      }else{
        return this.$http.server_host + image_url;
      }
    },
    initForm(banner){//初始化表单
      if(banner){ //编辑轮播图时，获取原有信息
        this.form.id=banner.id
        this.form.name=banner.name
        this.form.image_url=banner.image_url
        this.form.link_url=banner.link_url
        this.form.priority=banner.priority
      }else{
        this.form={ //添加轮播图时，将原有信息清空
          name:"",
          image_url:"",
          link_url:"",
          priority:0
        }
      }

    },
    onAddButtodClick(){
      this.initForm();
      this.bannderDialogVisible =true
    },
    onImageUploadSuccess(response){
      if(response['code']==200){
        var image_name = response['data']['image_url'];
        var image_url = "/media/banner/"+image_name;
        this.form.image_url =image_url;//跟 v-model="form.image_url" 绑定
      }

    },
    onImageUploadError(error, uploadFile, uploadFiles){
      console.log(error);
      console.log(uploadFile);
      console.log(uploadFiles);
    }, 
    onDialogSubmitEvent(){ //表单验证提交的方法  使用方法参考EP库中的form表单->表单校验
      this.$refs['dialogForm'].validate((valid)=>{
        if (!valid){
          ElMessage.error("请确保数据输入正确!") //验证失败的情况
          return;
        }
        //轮播图表单验证通过后
        if(this.form.id){
          //有id走编辑操作
          this.$http.editBanner(this.form).then((result)=>{
            let code = result['code'];
            if(code ==200){
              let banner = result['data']; //获取编辑后轮播图数据
              //this.banners.push(banner); //编辑就不是添加数据了
              //需要使用splice才能完成替换操作，不能通过直接修改下表修改元素，因为直接修改元素页面上不会自动改变
              this.banners.splice(this.editingIndex,1,banner)//这里表示把banners中对应的那1行删了，换成新的banner数据
              ElMessage.success("轮播图编辑成功！");
              this.bannderDialogVisible=false; //添加成功以后把对话框隐藏
            }
          })
        }else{
          //没有id走添加操作
          this.$http.addBanner(this.form).then((result)=>{
            let code = result['code'];
            if(code ==200){
              let banner = result['data']; //获取轮播图
              this.banners.push(banner); //添加到轮播图列表中
              ElMessage.success("轮播图添加成功！");
              this.bannderDialogVisible=false; //添加成功以后把对话框隐藏
            }
          }).catch(()=>{
            ElMessage.error("服务器开小差了，请稍后再试");
            this.bannderDialogVisible=false;
          })
        }
      })
    },
    onEditEvent(index){ //编辑轮播图的点击事件
      this.editingIndex = index;
      let banner = this.banners[index]; //获取被编辑的轮播图对象
      this.initForm(banner);
      this.bannderDialogVisible = true;

    },
    onDeleteEvent(index){ //删除轮播图点击事件
      this.deleteingIndex = index;  //轮播图的索引绑定到 数据deleteingIndex上，以便对话框的点击事件可以调用
      this.deleteDialogVisible=true;
    },
    onConfirmDeleteEvent(){ //确认删除轮播图的点击事件
      let banner = this.banners[this.deleteingIndex]  //取出要删除的轮播图
      this.$http.deleteBanner(banner.id).then(res => {
        let result = res['data'];
        let code = result['code'];
        if(code === 200){
          this.deleteDialogVisible = false;
          this.banners.splice(this.deleteingIndex, 1); //数组中删除 1表示删除1个
          ElMessage.success("轮播图删除成功！");
        }
      })
    },
  }
};
</script>

<style scoped>
.el-space {
  display: block;
}
</style>
