import { createWebHashHistory, createRouter } from 'vue-router'//参考文档https://router.vuejs.org/zh/guide/ 的入门部分
import Home from '@/pages/Home.vue'
import Post from '@/pages/Post.vue';
import Comment from '@/pages/Comment.vue';
import Banner from '@/pages/Banner.vue';
import User from "@/pages/User.vue"


const routes = [
    { path: '/', component: Home , name:"home"},  //指定了name以后就直接用name指向该url
    { path: '/banner', component: Banner , name:"banner"},
    { path: '/comment', component: Comment , name:"comment"},
    { path: '/post', component: Post , name:"post"}, 
    { path: '/user', component: User , name:"user"}, 
  ];

  const router = createRouter({
    history: createWebHashHistory(),
    routes,
  });

  export default router; //显式导出