<template>
  <div id="home"> 
    <h1>首页</h1>
  <div class="chart" id="board-post-count"></div>
  <div class="chart" id="day7-post-count"></div>
  </div>
</template>

<script>
import * as echarts from 'echarts'; //图表代码来源于echart  https://echarts.js.cn/zh/index.html 中的实例，直接套用改id/getElementById和data就行
import { ElMessage } from 'element-plus';
export default {
  name:"HomePage",
  mounted(){  
    this.loadBoardPostCountChat();
    this.load7DayPostCount();
  },
  methods:{
    loadBoardPostCountChat(){
      this.$http.getBoardPostCount().then(res =>{
        if(res["code"]!=200){
          ElMessage.error(res['message']);
          return
        }
        var data = res['data']
        var board_names= data['board_names']
        var post_counts= data['post_counts']
        var chartDom = document.getElementById('board-post-count'); //图表id跟div中的对应
        var myChart = echarts.init(chartDom);
        var option;
        option = { //想加别的option直接找找别的实例里面有没有，然后依葫芦画瓢
          title:{
            text:"板块帖子数量",
          },
          tooltip:{
            trigger:'axis'
          },
          xAxis: {
            type: 'category',
            data: board_names
          },
          yAxis: {
            type: 'value'
          },
          series: [
            {
              data: post_counts,
              type: 'bar'
            }
          ]
        };
      option && myChart.setOption(option);
    })
    },
    load7DayPostCount(){
      this.$http.get7DayPostCount().then(res =>{
        if (res['code']!=200){
          ElMessage.error(res["message"]);
          return
        }
        var data=res['data']
        var dates = data['dates']
        var counts = data['counts']
        var chartDom = document.getElementById('day7-post-count');
        var myChart = echarts.init(chartDom);
        var option;

        option = {
          title:{
            text:"近七天发帖数量"
          },
          tooltip:{
            trigger:'axis'
          },
          xAxis: {
            type: 'category',
            boundaryGap: false,
            data: dates
          },
          yAxis: {
            type: 'value'
          },
          series: [
            {
              data: counts,
              type: 'line',
              areaStyle: {}
            }
          ]
        };
      option && myChart.setOption(option);
    
    });
    }
    }
}
</script>

<style scoped>
  .chart{
    height: 300px; 
    width: 100%;
  }
</style>
