<!-- 网页长什么样由App.vue决定 -->
<template>
  <div class="frame">
    <el-container class="frame-container">
      <el-header class="header">
        <a href="/" class="brand"><strong>知了</strong>管理系统</a>
        <div class="header-content">
          <div class="greet">欢迎，{{$auth.user.username}}[{{ $auth.user.role.name }}]
           <!--  <span class="iconfont icon-post"></span>使用iconfont中组件的固定方法，记得先到index.html中导入官网生成的css文件 -->
          </div>
          <div class="signout">回到首页</div>
        </div>
      </el-header>
      <el-container>
        <el-aside width="200px" class="aside">
          <el-row class="menu-row">
            <el-col :span="24">
              <el-menu
                default-active="1"
                background-color="#545c64"
                active-text-color="#fff"
                text-color="#ddd"
                :router="true"
              ><!-- router为js表达式，前面要加： :router="true"开启跳转链接-->
                <el-menu-item index="1" :route="{name: 'home'}"><!-- :route="{name: 'home'}跳转到名为home的url -->
                  <template #title>
                    <el-icon><House /></el-icon><!-- 插入EP库中icon组件House -->
                    <span>首页</span>
                  </template>
                </el-menu-item>

                <el-menu-item index="2" :route="{name: 'banner'}" v-if="has_permission('banner')">
                  <template #title>
                    <el-icon><PictureRounded /></el-icon>
                    <span>轮播图</span>
                  </template>
                </el-menu-item>

                <el-menu-item index="3" :route="{name: 'post'}" v-if="has_permission('post')">
                  <template #title>
                    <el-icon><Postcard /></el-icon>
                    <span>帖子管理</span>
                  </template>
                </el-menu-item>

                <el-menu-item index="4" :route="{name: 'comment'}" v-if="has_permission('comment')">
                  <template #title>
                    <el-icon><Comment /></el-icon>
                    <span>评论管理</span>
                  </template>
                </el-menu-item>

                <el-menu-item index="5" :route="{name: 'user'}" v-if="has_permission('user')">
                  <template #title>
                    <el-icon><User /></el-icon>
                    <span>用户管理</span>
                  </template>
                </el-menu-item>
              </el-menu>
            </el-col>
          </el-row>
        </el-aside>
        <el-container>
          <el-main class="main">
            <router-view></router-view>
          </el-main>
          <el-footer class="footer">我们不是牛马，我们是人！！！！！</el-footer>
        </el-container>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import {House,PictureRounded,Postcard,Comment,User } from "@element-plus/icons" 
export default {
  name: "App",
  components:{
    House,
    PictureRounded,
    Postcard,
    Comment,
    User 
  },//导入icons组件，需要放到components中 否则<el-icon><User /></el-icon>显示不出图标
  
  mounted(){ //生命周期方法,用于table组件的自动循环，挂载  参考文档：https://www.zlkt.net/book/detail/13/354
    if(!this.$auth.is_staff){
      window.location=this.$http.server_host;//非员工就跳转回首页 
      //这里测试的时候可以将5000端口的两个key拷贝到8080中，测试登陆后会不会跳转回首页（注意jwt_token的过期时间）
    }
  },
  methods:{
    //权限判断方法 
    has_permission(permission){ //permissions列表由login视图传入
      return this.$auth.user.permissions.indexOf(permission)>=0 //相对应的权限在permissions列表中的索引>=0则表示用户有该权限
    }
  }
};
</script>

