import { createApp } from 'vue'
import App from './App.vue'
import ElementPlus from 'element-plus' //引入element-plus组件库
import 'element-plus/dist/index.css'
import router from '@/router'
import auth from '@/utils/auth'
import http from "@/utils/http"
//main.js是整个程序的入口文件，html文件执行完后执行次此文件



const app =createApp(App)
app.use(ElementPlus)
app.use(router) //参考文档https://router.vuejs.org/zh/guide/ 的入门部分
app.config.globalProperties.$auth=auth;  //绑定全局属性，在所有组件中都可通过this.auth访问Auth.getInstance() 对象
app.config.globalProperties.$http=http;
app.mount('#app')



//下面是解决runtime error
const debounce = (fn, delay) => {
    let timer
     return (...args) => {
       if (timer) {
         clearTimeout(timer)
       }
       timer = setTimeout(() => {
         fn(...args)
       }, delay)
     }
  }
    
const _ResizeObserver = window.ResizeObserver;
window.ResizeObserver = class ResizeObserver extends _ResizeObserver{
constructor(callback) {
callback = debounce(callback, 200);
super(callback);
}
}