const USER_KEY = "USER_KEY"
const TOKEN_KEY = "JWT_TOKEN_KEY"  //前台中的login.js传了这两个key到本地存储

class Auth {
  constructor() { //构造函数
    this.token = null
    this.user = null
    this.token = localStorage.getItem(TOKEN_KEY)
    const userJson = localStorage.getItem(USER_KEY)
    if (userJson) {
      this.user = JSON.parse(userJson) //user_key是json格式的字符串，通过parse得到user这个object
    }
  }

  static getInstance() {
    if (!this._instance) {
      this._instance = new Auth()
    }
    return this._instance
  }

  setUserToken(user, token) {
    this.user = user
    this.token = token
    localStorage.setItem(USER_KEY, JSON.stringify(user))
    localStorage.setItem(TOKEN_KEY, token)
  }

  clearUserToken() {
    this.user = null;
    this.token = null;
    localStorage.removeItem(USER_KEY)
    localStorage.removeItem(TOKEN_KEY)
  }

  get is_authed() {  //将is_authed定义为属性，跟python中@property是相同的原理   user.is_authed即可不用user.is_authed()
    if (this.user && this.token) { //用户存在并有token则为登录状态
      return true
    } else {
      return false
    }
  }

  get is_staff() {
    if (!this.is_authed) { //用户未登录
      return false;
    }
    if (this.user.is_staff) {
      return true;
    }
    return false;
  }
}


export default Auth.getInstance()  //导出